import md5 from 'js-md5';


const STORAGE_USER_KEY = 'STORAGE_USER_KEY'
// const STORAGE_CARTLIST_KEY = 'STORAGE_CARTLIST_KEY'
// const STORAGE_QUERYMYLIST_KEY = 'STORAGE_QUERYMYLIST_KEY'

export default {
  // 获取
  getLocal (key = STORAGE_USER_KEY) {
    // console.log('get local operation')
    return JSON.parse(window.localStorage.getItem(key))
  },
  // 设置用
  saveLocal (res, key = STORAGE_USER_KEY) {
    return window.localStorage.setItem(key, JSON.stringify(res))
  },
  paraAddKey (res,username,password) {
    res['api_user']=username;
    res['api_time_sys']=new Date().getTime();
    var res_new = Object.keys(res).sort();
    var str='';
    for (var k in res_new)
    {
      if(str!='')str=str+'&';
      str=str+res_new[k]+'='+res[res_new[k]];
    }
    res['key']=md5(str+password);

    return res;
  },
  setLocal (res, key = STORAGE_USER_KEY, isSaveOldData = false) {
    //第三个参数是true的话,会增加数据而不是重新设置,res必须是数组
    if (isSaveOldData) {
      if(this.getLocal(key)) {
        let oldData = this.getLocal(key);
        let IsDeal=false;
        for(let i in oldData)
        {
          if(oldData[i]['id']==res[0]['id'] && oldData[i]['type']['ID']==res[0]['type']['ID'])
          {
            oldData[i]['count']=oldData[i]['count']+1;
            IsDeal=true;
          }
        }
        if(!IsDeal)oldData=oldData.concat(res);
        return window.localStorage.setItem(key, JSON.stringify(oldData))
      }
    }
    return window.localStorage.setItem(key, JSON.stringify(res))
  },

  Cheng(num1, num2) {  
    var baseNum = 0;  
    try {  
        baseNum += num1.toString().split(".")[1].length;  
    } catch (e) {  
    }  
    try {  
        baseNum += num2.toString().split(".")[1].length;  
    } catch (e) {  
    }  
    return Number(num1.toString().replace(".", "")) * Number(num2.toString().replace(".", "")) / Math.pow(10, baseNum);  
  },
  Jia(num1, num2) {  
    var baseNum, baseNum1, baseNum2;  
    try {  
        baseNum1 = num1.toString().split(".")[1].length;  
    } catch (e) {  
        baseNum1 = 0;  
    }  
    try {  
        baseNum2 = num2.toString().split(".")[1].length;  
    } catch (e) {  
        baseNum2 = 0;  
    }  
    baseNum = Math.pow(10, Math.max(baseNum1, baseNum2));  
    return (num1 * baseNum + num2 * baseNum) / baseNum;  
  },
  Chu(num1, num2) {  
    var baseNum1 = 0, baseNum2 = 0;  
    var baseNum3, baseNum4;  
    try {  
        baseNum1 = num1.toString().split(".")[1].length;  
    } catch (e) {  
        baseNum1 = 0;  
    }  
    try {  
        baseNum2 = num2.toString().split(".")[1].length;  
    } catch (e) {  
        baseNum2 = 0;  
    }
    baseNum3 = Number(num1.toString().replace(".", ""));  
        baseNum4 = Number(num2.toString().replace(".", ""));  
        return (baseNum3 / baseNum4) * pow(10, baseNum2 - baseNum1); 
  },
  Jian(num1, num2) {  
    var baseNum, baseNum1, baseNum2;  
    var precision;// 精度  
    try {  
        baseNum1 = num1.toString().split(".")[1].length;  
    } catch (e) {  
        baseNum1 = 0;  
    }  
    try {  
        baseNum2 = num2.toString().split(".")[1].length;  
    } catch (e) {  
        baseNum2 = 0;  
    }  
    baseNum = Math.pow(10, Math.max(baseNum1, baseNum2));  
    precision = (baseNum1 >= baseNum2) ? baseNum1 : baseNum2;  
    return ((num1 * baseNum - num2 * baseNum) / baseNum).toFixed(precision);  
} 














}



