import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import router from './router'
import store from '@/vuex/store.js'   //vuex
//import api from '@/http/api.js'       //http请求
import fly from '@/http/api-fly.js'       //http请求
import less from 'less'
import Mint from 'mint-ui';    //移动端UI
import 'mint-ui/lib/style.css'
import conf from '@/conf.js';
import { Toast } from 'mint-ui'
import Util from '@/util/common'
Vue.use(Mint,fly);
Vue.config.productionTip = false
//Vue.prototype.$api = api;
Vue.prototype.$conf = conf;
Vue.prototype.$dopost = function(url,data,fn)//sign
{
  var _sign = arguments[3] ? arguments[3] : false;
  if(_sign===true)
  {
    data=Util.paraAddKey(data,store.state.login.username,store.state.login.password);
    //console.log(data);
  }
	if(data && typeof data=='object')
	{
		var _data=new URLSearchParams();
		for(var i in data) 
		{
        	_data.append(i, data[i]);
    }
		data=_data;
	}
	data = data || {} ;
	fly.post(url, data).then(fn)
	.catch(function (error) {Toast('接口异常:'+error);});
}

Vue.prototype.$isEmptyObject = function(obj) {
  for (var key in obj) {
      return false;
  }
  return true;
}

Vue.prototype.$Cheng = function(num1, num2) {  
  var baseNum = 0;  
  try {  
      baseNum += num1.toString().split(".")[1].length;  
  } catch (e) {  
  }  
  try {  
      baseNum += num2.toString().split(".")[1].length;  
  } catch (e) {  
  }  
  return Number(num1.toString().replace(".", "")) * Number(num2.toString().replace(".", "")) / Math.pow(10, baseNum);  
};
Vue.prototype.$Jia = function(num1, num2) {  
  var baseNum, baseNum1, baseNum2;  
  try {  
      baseNum1 = num1.toString().split(".")[1].length;  
  } catch (e) {  
      baseNum1 = 0;  
  }  
  try {  
      baseNum2 = num2.toString().split(".")[1].length;  
  } catch (e) {  
      baseNum2 = 0;  
  }  
  baseNum = Math.pow(10, Math.max(baseNum1, baseNum2));  
  return (num1 * baseNum + num2 * baseNum) / baseNum;  
};
Vue.prototype.$Chu = function(num1, num2) {  
  var baseNum1 = 0, baseNum2 = 0;  
  var baseNum3, baseNum4;  
  try {  
      baseNum1 = num1.toString().split(".")[1].length;  
  } catch (e) {  
      baseNum1 = 0;  
  }  
  try {  
      baseNum2 = num2.toString().split(".")[1].length;  
  } catch (e) {  
      baseNum2 = 0;  
  }
  baseNum3 = Number(num1.toString().replace(".", ""));  
      baseNum4 = Number(num2.toString().replace(".", ""));  
      return (baseNum3 / baseNum4) * pow(10, baseNum2 - baseNum1); 
};

Vue.prototype.$isEmpty = function(_store,_para,_default)
{
  if(!_store[_para] || typeof(_store[_para]) == "undefined")
  {
    _store[_para]=_default;
  }
  return _store[_para];
}


// 用钩子函数beforeEach()对路由进行判断

router.beforeEach((to, from, next) => {

    if (to.meta.requireAuth) {  // 需要权限,进一步进行判断
      if (store.state.login.token) {  // 通过vuex state获取当前的token是否存在
        next();
      }
      else {    //如果没有权限,重定向到登录页,进行登录
        next({
          path: '/login',
          // query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
        })
      }
    }
    else { //不需要权限 直接跳转
      next();
    }
})

router.afterEach((to,from,next) => {
  document.title = to.name || '';
  
})


new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App },
  beforeCreate: function () {
    let loginedUser=Util.getLocal('loginedUser');
    if(loginedUser && loginedUser[0] && loginedUser[1])
    {
      this.$store.commit('CHANGE_USER',[1,loginedUser[0],loginedUser[1]]);
    }
  } 
})



