import Util from '../../util/common'
import * as types from '../types'

const state = {
  token:0,
  username:'',
  password:''
}

const mutations = {
  [types.CHANGE_USER] (state,para) {
    state.token = para[0];
    state.username = para[1];
    state.password = para[2];
    Util.setLocal([state.username,state.password],'loginedUser');
  }
}


export default {
  state,
  mutations
}



